import { Component, OnInit } from '@angular/core';
import { IProduct } from 'src/app/interfaces/product';
import { ProductsService } from 'src/app/services/products.service';
import { FormsModule } from '@angular/forms';


@Component({
  selector: 'app-test-products',
  templateUrl: './test-products.component.html',
  styleUrls: ['./test-products.component.css']
})
export class TestProductsComponent implements OnInit {

  products: Array<IProduct>;
  product = {};
  modalData = {};

  constructor(private productsService: ProductsService) {
    this.getProducts();
  }

  ngOnInit() { }

  private getProducts(): void {
    this.productsService.getProducts().subscribe(
      data => {
        this.products = data;
      },
      err => {
        console.log(err)
      }
    )
  }

  public deleteProduct(id: number) {
    this.productsService.delProduct(id).subscribe(
      data => this.product = data,
      err => console.log(err)
    )
    this.getProducts();
  }

  public addedProduct(product: IProduct) {
    this.productsService.addProduct(product).subscribe(
      data => this.products.push(<IProduct>data),
      err => console.log(err)
    )
  }

  public updateProduct(id: number) {
    this.productsService.getOneProduct(id).subscribe(
      data => {
        this.modalData = data[0];
        console.log(data[0])
      },
      err => console.log(err)
    )
  }

  public clearModal() { this.modalData = null; }

  public updateProductAction(product: IProduct): void {
    this.productsService.updateProduct(product.id, product).subscribe(
      data => alert(`product #${product.id} | ${product.title} have been updated`),
      err => console.log(err)
    )
  }
}
