import { Injectable } from '@angular/core';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { Observable } from 'rxjs';
import { IProduct } from '../interfaces/product';


@Injectable({
  providedIn: 'root'
})
export class ProductsService {
  private url: string;
  constructor(private http: HttpClient) {
    this.url = 'http://localhost:4900/api';
  }
  public getProducts(): Observable<Array<IProduct>> {
    return this.http.get<Array<IProduct>>(`${this.url}/products`);
  }

  public addProduct(product: IProduct): Observable<IProduct> {
    return this.http.post<IProduct>(`${this.url}/product/create`, product)
  }

  public delProduct(id: number): Observable<Array<IProduct>> {
    return this.http.delete<Array<IProduct>>(`${this.url}/product/delete/${id}`)
  }

  public updateProduct(id: number, product: IProduct): Observable<IProduct> {
    return this.http.put<IProduct>(`${this.url}/product/edit/${id}`, product)
  }

  public getOneProduct(id: number): Observable<IProduct> {
    return this.http.get<IProduct>(`${this.url}/product/${id}`);
  }
}
